h1,
h2,
h3,
h4 {
  font-weight: 600;
  margin-block-end: 0.5em;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

:root,
[data-theme=light] {
  --black: #000000;
  --white: #ffffff;
  --gray-50: #f2f2f2;
  --gray-100: #e6e6e6;
  --gray-200: #cccccc;
  --gray-300: #b3b3b3;
  --gray-400: #999999;
  --gray-500: #808080;
  --gray-600: #666666;
  --gray-700: #4d4d4d;
  --gray-800: #333333;
  --gray-900: #1a1a1a;
  --gray-950: #0d0d0d;
  --primary: #0d6efd;
  --secondary: #666666;
  --success: #198754;
  --info: #0dcaf0;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #e6e6e6;
  --dark: #1a1a1a;
}

html {
  font-size: 1rem;
}

body {
  font-size: 1rem;
  font-family: Arial, Helvetica, sans-serif;
  color: #333333;
}

.progress-bar {
  background-color: #3eac87;
  inset-block-start: 0;
  position: fixed;
  block-size: 8px;
  inset-inline-start: 0;
}

:root {
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

.container {
  padding-inline: 12px;
}

.restaurant-item + .restaurant-item {
  margin-block-start: 48px;
}

main {
  margin-block: 48px;
}

header {
  height: 100px;
  background-color: #3eac87;
  color: #ffffff;
}

footer {
  block-size: 100px;
  background-color: #333333;
  color: #ffffff;
}

.dish {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 12px;
  transition: 0.3s;
  border: 1px solid #cccccc;
  border-radius: 24px;
}
.dish .image {
  block-size: 100px;
  inline-size: 100px;
  background-color: #999999;
  border-radius: 12px;
  overflow: hidden;
}
.dish .image * {
  inline-size: 100%;
  block-size: 100%;
  object-fit: cover;
}
.dish .desc {
  display: flex;
  flex-direction: row;
  gap: 24px;
}
.dish .ingredients {
  display: flex;
  flex-direction: row;
  gap: 4px;
  margin-block-start: 8px;
  font-size: 0.875rem;
  color: #808080;
}
.dish li:not(:last-child):after {
  content: ", ";
}
.dish .ecom {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
  justify-content: center;
}
.dish .price {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 4px;
  font-weight: 600;
  font-size: 1.25rem;
  color: #399173;
}
.dish .price span {
  font-size: 1rem;
}
.dish .counter {
  align-self: center;
}
.dish .counter span {
  font-weight: 600;
}
.dish button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #399173;
  transition: 0.3s;
  line-height: 1;
  color: #ffffff;
}
.dish button:hover {
  background-color: #3eac87;
}

@media (min-width: 576px) {
  .dish {
    flex-direction: row;
    align-items: stretch;
  }
  .dish .wrap {
    justify-content: space-between;
  }
  .dish .counter {
    align-self: end;
  }
}
.dishes {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-block-end: 24px;
  max-inline-size: 600px;
}

.counter {
  display: inline-flex;
  flex-direction: row;
  gap: 12px;
  align-items: center;
  justify-content: space-between;
  width: 120px;
}

.counterBtn {
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 8px;
  font-size: 24px;
}

.tabBtns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 24px;
  margin-block-end: 24px;
}
.tabBtns button {
  background-color: #399173;
  border: none;
  padding: 12px 24px;
  font-weight: 600;
  color: #ffffff;
  border-radius: 12px;
}

.review-form {
  margin-block-start: 36px;
}

.form {
  margin-block-start: 24px;
}
.form > div {
  display: flex;
  flex-direction: row;
  margin-block-end: 24px;
  align-items: flex-end;
}
.form > div > span {
  font-size: 1.25rem;
  font-weight: 600;
  inline-size: 120px;
  padding-block-end: 4px;
  flex-shrink: 0;
}
.form input {
  border-radius: 4px;
  border: 1px solid #999999;
  padding: 8px 16px;
  inline-size: 100%;
}
.form textarea {
  border-radius: 4px;
  border: 1px solid #999999;
  padding: 8px 16px;
  inline-size: 100%;
  resize: vertical;
}
.form .clear-form {
  background-color: #ffffff;
  border: none;
  padding: 12px 24px;
  font-weight: 600;
  color: #399173;
  border: 1px solid #399173;
  border-radius: 12px;
  transition: 0.3s;
  cursor: pointer;
}
.form .clear-form:hover {
  background-color: #399173;
  color: #ffffff;
}
.form .counter button {
  cursor: pointer;
  color: #ffffff;
  background-color: #bd8f4a;
  transition: 0.3s;
}
.form .counter button:hover {
  background-color: #d6983c;
}
.form .counter span {
  font-weight: 600;
}